<template>
	<div class="main_vue">
	  <header-vue></header-vue>
      <router-view></router-view>
	</div>

</template>

<script>
import Header from './Header.vue'
export default {
  components: {
  	'header-vue':Header
  },
  name: 'Main',
  data () {
    return {
      msg: 'Main'
    }
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

 html,body{
	font-family: Roboto;
}

/*@font-face{
  font-family: digitals;
  src:url('../../static/fonts/DIGITAL-7MONO.TTF') format('truetype');
  font-weight: normal;
  font-style: normal;
}*/

.box{
	border-radius: 4px;
	background-color: white
}
body{
	background-color: #dfdfe0;
} 
.left{
	float: left;
}
.right{
	float: right;
}
ul,p{
	margin:0;
	padding:0;
}

li{
	list-style: none;
}

.bqd-nav{
	margin-bottom: 0;
	background-color: #3d5f7f;
	border-bottom:0;
}
.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover{
	background-color: #3d5f7f;
}
.navbar .navbar-brand{
	color: #f0f0f0;
	font-weight: 600;
	padding:8px 15px 8px 80px;
	position: relative;
	line-height: 34px;
}
.navbar-brand:hover,.navbar-brand:focus{
	color: #fff;
}
.navbar-logo{
	position: absolute;
	display: inline-block;
	width: 61px;
	height: 41px;
	left:15px;
	top:5px;
	background:url(../assets/images/logo.png) no-repeat;
	background-size: 100% 100%;
}
.navbar-center{
	line-height: 50px;
	text-align: center;
}
.navbar-title{
	color: #f0f0f0;
	font-size: 16px;
}
.navbar-center li{
	text-align:left;
}
.navbar .navbar-nav>li>a{
	color: #eee;
}
.navbar-nav>li>a:hover,.navbar-nav>li>a:focus{
	background-color: transparent;
}
.navbar-nav>li>a.active{
	color: #2fbdff;
}
.navbar .nav .open>a,.navbar .nav .open>a:focus{
	background-color: transparent;
}
.clearfix:after{
	content:"";
	height:0;
	line-height: 0;
	display: block;
	visibility: hidden;
	clear:both;
}
.clearfix{
	zoom:1;
}


.main{
	background-color: #dfdfe0;
	padding:15px;
}
.bqd-part{
	margin-bottom:15px;
	background-color: #fff
}
.bqd-part-bg{
	background-color: #fff;
	border-radius: 4px;
}
.bqd-part-mg{
	margin-top: 5px;
}
.bqd-bg-spec{
	background-color: #fff;
	border-radius: 4px;
	padding-left: 15px;
	padding-right: 15px;
	margin-right: -15px;
}
.panels-6{
	width: 60%;
	float: left;
	position: relative;
}
.panels-4{
	width: 40%;
	float: left;
}
.bqd-title .text-bg{
	font-size: 18px;
}
.bqd-title .text-large{
	height: 35px;
	line-height: 35px;
}
.bqd-title .text-big{
	height: 40px;
	line-height: 40px;
}
.bqd-title .text .bj{
	display: inline-block;
	width: 6px;
	height: 20px;
	background-color: #d54a4a;
	border-radius: 6px;
	margin-right: 8px;
	position: relative;
	top: 4px;
}
.bqd-title .text .bj-blue{
	background-color:#0992ca;
}
.bj-more{
	color: #737272;
	line-height: 35px;
}
.product{
	width:75%;
	height: 102px;
	position: relative;
}
.product-tips{
	position: absolute;
	bottom:10px;
	right:0;
	width: 25%;
	height: 92px;
	box-sizing: border-box;
	border:1px solid #bbb;
}
.product-tips ul{
	padding:9px 6px;
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-around;
	height: 100%;
	box-sizing: border-box;
}
.product-tips ul li{
	font-size: 16px;
	color: #101010;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	text-align: center;
}
.product-tips ul li .title{
	font-size: 12px;
}
.product-count{
	padding:0 5px;
}
.product-count li{
	float: left;
	width: 33.33%;
}
.product-count h4{
	color: #101010;
	font-size: 14px;
}
.product-count .product-count-pie{
	height: 102px;
}
.bqd-warn{
	padding-bottom:12px;
}
.bqd-context{
	padding:0px;
	border:1px solid #bbb;
}
.bqd-context .status-normal{
	display: flex;
	justify-content: center;
	display: none;
}
.bqd-context .status-normal .warn-text1{
	font-size: 38px;
	color: #00cc00;
	margin:17px 0;
	text-align: center;
	line-height: 54px;
}
.bqd-context .status-danger .warn-text1{
	font-size: 38px;
	color: #d50909;
	width: 100%;
	text-align: center;
	line-height: 54px;
}
.bqd-context .status-danger .warn-text2{
	font-size: 18px;
	color:#737272;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	width: 50%;
	float: left;
	text-align: center;
	line-height: 34px;
}
.bqd-context .status-danger .warn-text3{
	font-size: 18px;
	color:#737272;
	width: 50%;
	float:left;
	text-align: center;
	line-height: 34px;
}
.warn-icon{
	margin-right: 5px;
	margin-bottom: 5px;
}
.camouflage{
	font-family:digitals;
	font-size:28px;
}
.camouflage li{
	float:left;
}
.camouflage .part1{
	color: #00edff;
	margin-right: 15px;
}
.camouflage .part2{
	color: #000;
}
.realtime-context{
	width: 100%;
	height: 405px;
}
.realtime-detail{
	display: flex;
	justify-content: space-around;
}
.realtime-item{
	float:left;
	flex:1;
	background-color: #fff;
	margin-right:5px;
	border-radius: 4px;
	padding-left: 15px;
	padding-right: 15px;
}
.realtime-item:last-child{
	margin-right:0;
}
.realtime-detail-context{
	height:180px;
}
.finished-product{
	margin-bottom: 5px;
}
.quality-grade-context{
	height: 240px;
}
.weight-dist-context{
	height: 150px;
}
.size-dist-context{
	height: 160px;
}
@media (max-width: 991px){
	.bqd-part{
		margin-bottom: 5px;
	}
	.bqd-part-bg{
		margin-bottom: 5px;
	}
	.bqd-bg-spec{
		margin-left: -15px;
	}
	.finished-product{
		margin-top:5px;
	}
}
@media (max-width: 768px){
	.navbar-title{
		display: none;
	}
}
@media (max-width: 767px){
	.navbar .nav .open .dropdown-menu>li>a{
		color: #fff;
	}
	.panels-6,.panels-4{
		width:100%;
	}
	.product-tips ul li{
		font-size: 12px;
	}
}
@media (max-width: 430px){
	.navbar .navbar-brand{
		display: none;
	}
}
@media (min-width: 1120px){
	.bqd-context{
		padding-left: 15px;
		padding-right: 15px;
	}
}
.fa{
	margin: 0 5px;
}

</style>



