<template>
	<div class="head_vue">
		
		
		<header class="navbar navbar-inverse navbar-static-top bqd-nav">
			<div class="container-fluid">
				<div class="navbar-header">
					<button class="navbar-toggle" type="button" data-toggle="collapse" data-target="#top">
						<span class="sr-only">Toggle navigation</span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
						<span class="icon-bar"></span>
					</button>
			<!-- 		<a href="javascript:;" class="navbar-brand">
						
					</a> -->
					<router-link to="/Devices"  class="navbar-brand">宁波百琪达智能科技（宁波）股份有限公司（BDM-350-Y）</router-link>
					<span class="navbar-logo"></span>
				</div>
				<nav class="navbar-collapse collapse navbar-center" id="top">
					
					<ul class="nav navbar-nav navbar-right">
						<li v-for="item in nav" @click="selectNav(item.title)">
							<router-link to="/"  :class="isSelect === item.title ? 'active' : ''">{{item.title}}</router-link>
						</li>
			
						<li class="dropdown">
							<a class="dropdown-toggle" data-toggle="dropdown" href="#" role="button">
								<i class="fa fa-user"></i><span class="caret"></span>
							</a>
							<ul class="dropdown-menu">
								<li><a href="#">个人中心</a></li>
								<li><a href="#">注销</a></li>
							</ul>
						</li>
					</ul>
				</nav>
			</div>
		</header>
	</div>
</template>

<script>
	export default {
		name: 'Header',
		data () {
			return {
				isSelect: '实时监控',
				nav: [
				{title: '实时监控'},
				{title: '工艺分析'},
				{title: '质量分析'},
				{title: '辅助调试'},
				{title: '历史回看'},
				{title: '故障检测'}
				]
			}

		},
		method () {
             this.isSelect = sessionStorage.getItem('isSelect')
		},
		methods: {
			selectNav (title) {
				this.isSelect = title
				switch (title) {
					case '实时监控': this.$router.push('/')
					break
					case '工艺分析': this.$router.push('/Tech')
					break
					case '质量分析': this.$router.push('/Quality')
					break
					case '辅助调试': this.$router.push('/Auxi')
					break
					case '历史回看': this.$router.push('/His')
					break
					case '故障检测': this.$router.push('/Fault')
					break
				}
				sessionStorage.setItem('isSelect', this.isSelect)

			}

		}
	}

	</script>

	<!-- Add "scoped" attribute to limit CSS to this component only -->
	<style scoped>
	.left{
		float: left;
	}
	.right{
		float: right;
	}
	ul,p{
		margin:0;
		padding:0;
	}

	li{
		list-style: none;
	}

	.bqd-nav{
		margin-bottom: 0;
		background-color: #3e82c6;
		border-bottom:0;
	}
	.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover{
		background-color: #3d5f7f;
	}
	.navbar .navbar-brand{
		color: #f0f0f0;
		font-weight: 600;
		padding:8px 15px 8px 80px;
		position: relative;
		line-height: 34px;
	}
	.navbar-brand:hover,.navbar-brand:focus{
		color: #fff;
	}
	.navbar-logo{
		position: absolute;
		display: inline-block;
		width: 61px;
		height: 41px;
		left:15px;
		top:5px;
		background:url(../assets/images/logo.png) no-repeat;
		background-size: 100% 100%;
	}
	.navbar-center{
		line-height: 50px;
		text-align: center;
	}
	.navbar-title{
		color: #f0f0f0;
		font-size: 16px;
	}
	.navbar-center li{
		text-align:left;
	}
	.navbar .navbar-nav>li>a{
		color: #eee;
	}
	.navbar-nav>li>a:hover,.navbar-nav>li>a:focus{
		background-color: transparent;
	}
	.navbar-nav>li>a.active{
		color: #fff;
		background-color: #2d6caa;
	}
	.navbar .nav .open>a,.navbar .nav .open>a:focus{
		background-color: transparent;
	}
	.clearfix:after{
		content:"";
		height:0;
		line-height: 0;
		display: block;
		visibility: hidden;
		clear:both;
	}
	.clearfix{
		zoom:1;
	}

</style>



