<template>
	<div class="devices_vue">
		<div class="container-fluid">
			<div class="row ">
				<div class="col-xs-12 col-sm-12 col-md-4 ">		
					<div class="jxd-title box"><i class="fa fa-pie-chart"></i>实时运行情况</div>				
					<div class="box dvc" id="dvc1">

					</div>		
				</div>
				
				<div class="col-xs-12 col-sm-12 col-md-8 ">
					<div class="jxd-title box"><i class="fa fa-line-chart"></i>当日各时段运行状态</div>	
					<div class="box dvc" id="dvc2">

					</div>		
					
				</div>

			</div>
			<div class="row">
				<div class="col-xs-12 col-sm-12 col-md-12 ">		

					<div class="box dvc_2">
						<div class="dvc_top">
							<div class="jxd-title1 box "><i class="fa fa-circle"></i>设备列表
							</div>
							<div class="dvc_status">
								<span><i class="fa fa-circle" style="color: red"></i>故障</span>
								<span><i class="fa fa-circle" style="color: green"></i>加工</span>
								<span><i class="fa fa-circle" style="color: yellow"></i>空闲</span>
								<span><i class="fa fa-circle" style="color: gray"></i>关闭</span>
							</div>
						</div>

						<ul class="devices_x">
							<li>
								<img src="" alt="正在加载">
								<span>6122</span>
							</li>
							<li>
								<img src="" alt="正在加载">
								<span>6122</span>
							</li>
							<li>
								<img src="" alt="正在加载">
								<span>6122</span>
							</li>
							<li>
								<img src="" alt="正在加载">
								<span>6122</span>
							</li>
						</ul>
						
					</div>			
					

					
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Devices_vue',
		data () {
			return {

			}

		},
		mounted () {
			var mychart1 = this.$echarts.init(document.getElementById("dvc1"))
			var mychart2 = this.$echarts.init(document.getElementById("dvc2"))
			function randomData() {
				now = new Date(+now + oneDay);
			    // value = value + Math.random() * 21 - 10;
			    var value = RandomNum(20, 50);
			    return {
			    	name: now.toString(),
			    	value: [
			    	[now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'),
			    	Math.round(value)
			    	]
			    }
			}
			function RandomNum(Min, Max) {
				var Range = Max - Min;
				var Rand = Math.random();
			      var num = Min + Math.floor(Rand * Range); //舍去
			      return num;
			  }
			  var data = [];
			  var data2 = [];
			  var data3 = [];
			  var now = +new Date();
			  var oneDay = 24 * 3600 * 1000;
			// var value = Math.random() * 30;
			for (var i = 0; i < 10; i++) {
				data.push(randomData());
				data2.push(randomData());
				data3.push(randomData());

			}

			var option1 = {
				tooltip: {
					trigger: 'item',
					formatter: "{a} <br/>{b}: {c} ({d}%)"
				},
				legend: {
					orient: 'vertical',
					left:'right',
					top:'30%',
					data:['生产','空闲','关机','故障']
				},
				series: [
				{
					name:'运行情况',
					type:'pie',
					radius: ['45%', '60%'],
					center: ['40%', '50%'],
					avoidLabelOverlap: false,

					data:[
					{value:30, name:'生产',
					itemStyle: {
						color: '#4ec089'
					},
					lineStyle: {
						color: '#4ec089'
					},
					label: {
						normal: {
							formatter: '{b}: {c} ({d}%)'
						}
					},
				},
				{value:20, name:'空闲',
				itemStyle: {
					color: '#51abd0'
				},
				lineStyle: {
					color: '#51abd0'
				},
				label: {
					normal: {
						formatter: '{b}: {c} ({d}%)'
					}
				},
			},
			{value:5, name:'关机',
			itemStyle: {
				color: '#f06900'
			},
			lineStyle: {
				color: '#f06900'
			},
			label: {
				normal: {
					formatter: '{b}: {c} ({d}%)'
				}
			},
		},
		{value:5, name:'故障',
		itemStyle: {
			color: '#d2d2d2'
		},
		lineStyle: {
			color: '#d2d2d2'
		},
		label: {
			normal: {
				formatter: '{b}: {c} ({d}%)'
			}
		},
	},

	]
}
]
};
var option2 = {

	tooltip: {
		trigger: 'axis',
		formatter: function (params) {
			params = params[0];
			var date = new Date(params.name);
			return date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear() + ' : ' + params.value[1];
		},
		axisPointer: {
			animation: false
		}
	},
	xAxis: {
		type: 'time',
		splitLine: {
			show: false
		}
	},
	yAxis: {
		type: 'value',
		boundaryGap: ['0','100%'],
		splitLine: {
			show: false
		}
	},
	series: [{
		name: '模拟数据',
		type: 'line',
		showSymbol: true,
		hoverAnimation: false,
		itemStyle: {
			color: '#51abd0'
		},
		lineStyle: {
			color: '#51abd0'
		},
		data: data
	},
	{
		name: '模拟数据',
		type: 'line',
		showSymbol: true,
		hoverAnimation: false,
		itemStyle: {
			color: '#f90e0e'
		},
		lineStyle: {
			color: '#f90e0e'
		},
		data: data2
	},
	{
		name: '模拟数据',
		type: 'line',
		showSymbol: true,
		hoverAnimation: false,
		itemStyle: {
			color: '#8bc34a'
		},
		lineStyle: {
			color: '#8bc34a'
		},
		data: data3
	}]
};

setInterval(function () {

	for (var i = 0; i <3; i++) {
		data.shift();
		data.push(randomData());
		data2.shift();

		data2.push(randomData());
		data3.shift();

		data3.push(randomData());


	}

	mychart2.setOption({
		series: [{
			data: data
		},{
			data: data2
		},{
			data: data3
		}]
	});
}, 5000);



mychart1.setOption(option1);
mychart2.setOption(option2);


setTimeout(function(){
	window.onresize = function(){
		mychart1.resize();
		mychart2.resize();


	}
},200);

}

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.devices_vue{
	padding: 15px;
	background-color: #dfdfe0;

}
.dvc{
	height: 232px;
	width: 100%;

	background-color: #fff;
}
.dvc_2{
	width: 100%;
	height: 600px;
	background-color: #fff;
	margin-top: 20px;
	float: left
}
.jxd-title{
	height: 40px;
	line-height: 40px;
	width: 100%;
	text-align: left;
	font-size: 1.2em;
	border-bottom: 1px solid #d2d2d2;
}
.jxd-title1{
	width: 50%;
	text-align: left;
	font-size: 1.2em;
	float: left;

	line-height: 40px;

}
.dvc_status{
	height: 30px;

	width: 50%;
	float: right;
	text-align: right;
	line-height: 40px;

}
.dvc_top{
	height: 40px;

}
.devices_x{
	width: 100%;
	height: 570px;
	
	padding: 10px;
	border-top: 3px solid #d2d2d2;


}
.devices_x>li{
	width: 136px;
	height: 151px;
	border: 1px solid #d2d2d2;
	float: left

}
.devices_x>li>img{
	display: block;
	height: 117px;
	width: 100%;
}
.devices_x>li>span{
	display: block;
	width: 100%;
	height: 34px;
	background-color: #bbbbbb;
	text-align: center;
	line-height: 34px;
}
</style>



