		border-bottom: 2px solid transparent;
	}
	.mainmenu-navbar-tabs>li.active>a{
		color: #3f51b5;
		border-bottom: 2px solid #3f51b5;
	}
	@media (min-width: 1300px){
		.leftSidebar{
			width: 12%;
		}
		.mainmenu{
			width: 88%;
			padding-left: 15px;
			padding-right:15px;
			margin-left: 12%;
		}
	}
	@media (max-width: 767px){
		.leftSidebar{
			position: static;
			height:373px;
			margin-bottom: 12px;
		}
		.mainmenu-bg{
