.warn_status_show{
	width: 20%;
	height: 35px;
	border: 1px solid #d2d2d2;
	border-radius: 4px;
	margin: 0 auto;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	line-height: 35px;
}
.warn_status_show>span{

}
.show_time{
	font-weight: 600;
	color: #f0dc00;
	font-size: 1.1em;
}
.show_info{
	font-size: 1em;
	font-weight: 600;	

}
.show_time2{
	font-size: 0.8em
}
.warn_type{

	width: 90%;
	height: 200px;
	/*margin: 0 auto;*/
	position: relative;
	left: 3%;

}
.warn_type>ul{
	width: 100%;
	height: 100%;
	position: absolute;
	left: -3%;
	top: 0;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
}
.warn_type>ul>li{
	width: 8.3%;
    border-left: 1px dashed #d2d2d2;
    transform: rotate(30deg)
}

.warn_fonts{
	width: 100%;
	height: 100%;
    font-size: 1.1em;
    font-weight: 600;
	position: absolute;
	left: 5%;
	top: 0;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;

}
.warn_fonts>span{
	width: 8.3%;
	display: inline-block;
	transform: rotate(-60deg)
}
.warn_fonts>span:nth-child(odd){
	color: #4ec089;
}
.warn_fonts>span:nth-child(even){
	color: #f06900;
}
.warn_fonts>span:nth-child(3n){
	color: #f0dc00;
}


.warn_type>ul>li:last-child{

	/*border-right: 1px solid #d2d2d2;*/
}

</style>



