import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Header from '@/components/Header'
import Devices from '@/components/Devices'
import Index from '@/components/Index'
import Tech from '@/components/Tech'
import Quality from '@/components/Quality'
import Auxi from '@/components/Auxi'
import His from '@/components/His'
import History1 from '@/components/sec/History1'
import History2 from '@/components/sec/History2'
import Fault from '@/components/Fault'
import Fupper from '@/components/sec/F-upper'
import Fdown from '@/components/sec/F-down'
import Fmag from '@/components/sec/F-mag'
import Fpush from '@/components/sec/F-push'
import Fweight from '@/components/sec/F-weight'
import Warn from '@/components/Warn'

Vue.use(Router)

export default new Router({
  // linkActiveClass: 'active_2',
  
  routes: [
  {
    path: '/',
    // redidect:'Index',
    name: 'Index',
    component: Index,
    children: [
    {
      path: 'Devices',
      component: Devices
    }, 
    {
      path: '',
      component: Main
    }, 
    {
      path: 'Tech',
      component: Tech
    },
    {
      path: 'Quality',
      component: Quality
    },
    {
      path: 'Auxi',
      component: Auxi
    },
    {
      path: 'His',
      component: His,
      children: [
      {
        path:'',
        alias:'History1',
        component:History1
      },
      {
        path:'History2',
        component:History2
      }

      ]
    },
    

    {
      path: 'Fault',
      name:'Fault',
      component: Fault,
      children: [
      {
        path:'',

        alias:'Fupper',
        component:Fupper
      },
      {
        path:'Fdown',
        component:Fdown
      },
      {
        path:'Fmag',
        component:Fmag
      },
      {
        path:'Fpush',
        component:Fpush
      },
      {
        path:'Fweight',
        component:Fweight
      },

      ]
    },
    {
      path: 'Warn',
      component: Warn
    }            
    ]
  }
  ]
})



