<template>
	<div class="fupper">
		<div class="">
			<div class="row">
				<div class="col-xs-12 col-sm-12 col-md-3 h358">
					<div class="box" id="fupper1" style="width: 100%;height: 100%"></div>
				</div>
				<div class="col-xs-12 col-sm-12 col-md-9 h358 ">
					<div class="box" id="fupper2" style="width: 100%;height: 100%"></div>
				</div>
			</div>

			<div class="row mt15">
				<div class="col-xs-12 col-sm-12 col-md-6 h497">
					<div class="box " id="fupper3" style="width: 100%;height: 100%"></div>
				</div>
				<div class="col-xs-12 col-sm-12 col-md-6 h497">
					<div class="box" id="fupper4" style="width: 100%;height:100%"></div>
				</div>
			</div>
		</div>
		
	</div>
</template>

<script>
	export default {
		name: 'F-upper',
		data () {
			return {
				msg: 'Welcome to Your Vue.js App'
			}
		},
		mounted () {
			var mychart1 = this.$echarts.init(document.getElementById("fupper1"));
			var mychart2 = this.$echarts.init(document.getElementById("fupper2"));
			var mychart3 = this.$echarts.init(document.getElementById("fupper3"));
			var mychart4 = this.$echarts.init(document.getElementById("fupper4"));

			var option1 = {
				title: {
					text: '雷达图',
					left: '5%',
					top: '5%',
					textStyle:{
						fontSize:16
					}
					
				},
				tooltip: {
					trigger: 'axis'
				},

				radar: [
				{
					indicator: [
					{text: '压力', max: 100},
					{text: '位置', max: 100},
					{text: '速度', max: 100},
					{text: '电流', max: 100}
					],
					center: ['50%','50%'],
					radius: 80
				}
				],
				series: [
				{
					type: 'radar',
					tooltip: {
						trigger: 'item'
					},
					itemStyle: {normal: {areaStyle: {type: 'default'}}},
					data: [
					{
						value: [60,73,85,40],
						name: '上缸'
					}
					]
				}
				]
			};
			var option2 = {
				title: {
					text: '预测图',
					left: '5%',
					top: '5%',
					textStyle:{
						fontSize:16
					}
					
				},
				xAxis: {
					type: 'value',
					splitNumber: 30,
					min: 0,
					max: 30,

					splitLine: {
						show: false
					}
				},
				yAxis: {
					type: 'category',
					splitNumber: 7,
					min: 0,
					max: 12,
					axisTick: {
						show: true
					},
					splitLine: {
						show: false
					},
					axisLabel: {
						show: false
					}
				},
				grid: {
					top: '15%',
					left: 30,
					right: 30,		
					bottom: 50
				},
				visualMap: {
					show: false,
					dimension: 0,
					pieces: [
					{
						gt: 0,
						lte: 6,
						color: '#a9a9a9'
					},
					{
						gt: 6,
						lte: 8,
						color: '#00fa9a'
					},
					{
						gt: 8,
						lte: 10,
						color: '#f0e68c'
					},
					{
						gt: 10,
						lte: 12,
						color: '#ff0000'
					}
					]
				},
				series: [
				{
					type: 'line',
					symbolSize: 0,
					smooth: true,
					data: [[1,2],[2,7],[3,5],[4,6],[5,8],[5,9],[6,1],[7,4]]
				}
				]
			}

			var option3 = {
				title: {
					text: '压力和位置',
					left: 5,
					top: 5,
					textStyle:{
						fontSize:16
					}
					
				},
				tooltip: {
					trigger: 'axis'
				},

				xAxis: {
					name: '压力',
					type: 'category',
					boundaryGap: false,
					data: [0, 2, 4 , 6 , 8 , 10 , 12]
				},
				yAxis: {
					name: '位置',
					type: 'value',
					data: [0,50,100,150,200,250,300],

				},
				series: [{
					name: '位置',
					type: 'line',
					smooth: true,
					data: [45,15,56,123,123,12]
				}
				]
			};
			var option4 = {
			    title: {
			        text: '压力和速度'
			    },
			    tooltip: {
			        trigger: 'axis'
			    },

			    grid: {
			        left: '3%',
			        right: '4%',
			        bottom: '3%',
			        containLabel: true
			    },
			   
			    xAxis: {
			    	name:"压力",
			        type: 'category',
			        data: [0,2,4,6,8,10,12]
			    },
			    yAxis: {
			    	name:"速度",
			        type: 'value',
			        data: [0,15,30,45,60,75,90]
			    },
			    series: [
			        {
			            name:'Step Start',
			            type:'line',
			            step: 'start',
			            data:[30,45,60,45,12,12,25]
			        }
			    ]
			};




			mychart1.setOption(option1);
			mychart2.setOption(option2);
			mychart3.setOption(option3);
			mychart4.setOption(option4);

			setTimeout(function(){
				window.onresize = function(){
					mychart1.resize();
					mychart2.resize();
					mychart3.resize();
					mychart4.resize();

				}
			},200);
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.h358{
	height: 358px;
}
.h497{
	height: 497px;
}
.mt15{
    margin-top: 15px;
}
</style>



