<template>
	<div class="fault">
		<div class="container-fluid">
			<div class="row ">
				<div class="col-xs-12 col-sm-12 col-md-1 ">					
					<ul class="fault_left box">
						<li class=""><i class="fa fa-microchip"></i>故障预测</li>
						<li>
							<router-link to="/Fault/Fweight" ><button type="button" class="btn btn-default fault_btn "><i class="fa fa-file "></i>称重</button></router-link>
							
						</li>
						<li>
							<router-link to="/Fault/Fupper" ><button type="button" class="btn btn-default  fault_btn " ><i class="fa fa-angle-double-up "></i>上缸</button></router-link>
							
						</li>
						<li>   
							<router-link to="/Fault/Fdown" ><button type="button" class="btn btn-default  fault_btn "><i class="fa fa-angle-double-down"></i>下缸</button></router-link>
							
						</li>
						<li>
							<router-link to="/Fault/Fmag" ><button type="button" class="btn  btn-default  fault_btn " ><i class="fa fa-eraser"></i>充磁</button></router-link>
							
						</li>
	                    <li>
							<router-link to="/Fault/Fpush" ><button type="button" class="btn btn-default  fault_btn " ><i class="fa fa-deaf"></i>推缸</button></router-link>
							
						</li>
					</ul>									
				</div>
				
				<div class="col-xs-12 col-sm-12 col-md-11 ">
					<router-view class=""></router-view>
				</div>

			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Fault',
		data () {
			return {
				msg: 'Welcome to Your Vue.js App'
			}
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.fault{
	padding: 15px;
	background-color: #dfdfe0;
	/*height: 100%;*/
}
.fault_left{
	background-color: white;
	border-radius: 4px;
	height: 870px;
}
.fault_left>li{
	width: 100%;
	height: 46px;
	border-radius: 4px;
	font-size: 1em
}
.fault_left>li:first-child{
	
	line-height: 46px;
	/*color: rgba(98, 97, 97, 1);*/
	background-color: white;
	font-size: 1em;
	font-weight: 600;
	text-align: center;
	font-family: Roboto;


}
.fault_btn{
	border: 0;
	border-radius: 0;
	width: 100%;
	height: 100%;

}
.ative_2{
	color: #638db4;
}
@media (max-width: 991px){
	.fault_left{
		height: 50px;
		margin-bottom: 10px;
	}
    .fault_left>li{
    	float: left;
    	width: 15%;
    	height: 50px;

    }
}
</style>



